"use client";
import React, { useState } from 'react';
import { motion, useMotionValue, useTransform, PanInfo } from 'framer-motion';
import { ArrowUpRight, ArrowDownRight, CheckCircle, Clock, Zap, X } from 'lucide-react';

const signals = [
    {
        id: 1,
        pair: "XAUUSD",
        side: "BUY",
        time: "1 hour ago",
        status: "PENDING",
        entry: 2038.50,
        sl: 2035.00,
        tp1: 2042.00,
        tp2: 2045.00,
        tp3: 2048.00,
        lotSize: 0.05,
        pnl: "+45 pips",
        pnlPositive: true
    },
    {
        id: 2,
        pair: "US30",
        side: "SELL",
        time: "3 hours ago",
        status: "EXECUTED",
        entry: 38500,
        sl: 38600,
        tp1: 38400,
        tp2: 38200,
        tp3: 38000,
        lotSize: 0.02,
        pnl: "+120 pips",
        pnlPositive: true
    }
];

const SwipeableSignalCard = ({ signal, onExecute, onReject }: any) => {
    const x = useMotionValue(0);
    const [swiped, setSwiped] = useState(false);

    const backgroundColor = useTransform(
        x,
        [-150, 0, 150],
        ['rgba(239, 68, 68, 0.1)', 'rgba(10, 12, 16, 1)', 'rgba(34, 197, 94, 0.1)']
    );

    const handleDragEnd = (event: any, info: PanInfo) => {
        if (info.offset.x > 150) {
            setSwiped(true);
            onExecute(signal);
        } else if (info.offset.x < -150) {
            setSwiped(true);
            onReject(signal);
        } else {
            x.set(0);
        }
    };

    if (swiped) return null;

    return (
        <motion.div
            className="relative mb-3"
            style={{ backgroundColor }}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
        >
            {/* Swipe Indicators */}
            <motion.div
                className="absolute left-4 top-1/2 -translate-y-1/2 flex items-center gap-2 text-red-400"
                style={{ opacity: useTransform(x, [0, -150], [0, 1]) }}
            >
                <X size={24} />
                <span className="font-bold text-sm">REJECT</span>
            </motion.div>

            <motion.div
                className="absolute right-4 top-1/2 -translate-y-1/2 flex items-center gap-2 text-green-400"
                style={{ opacity: useTransform(x, [0, 150], [0, 1]) }}
            >
                <span className="font-bold text-sm">EXECUTE</span>
                <Zap size={24} className="fill-current" />
            </motion.div>

            <motion.div
                drag="x"
                dragConstraints={{ left: 0, right: 0 }}
                dragElastic={0.2}
                onDragEnd={handleDragEnd}
                style={{ x }}
                className="relative overflow-hidden rounded-2xl border border-white/5 bg-[#0a0c10] p-5 cursor-grab active:cursor-grabbing touch-none"
            >
                <div className="absolute left-0 top-0 h-full w-1 bg-gradient-to-b from-blue-500 to-purple-500" />

                <div className="mb-4 flex items-start justify-between">
                    <div className="flex gap-3">
                        <div className="flex h-10 w-10 items-center justify-center rounded-full bg-blue-600 font-bold text-white shadow-lg shadow-blue-500/20">
                            PS
                        </div>
                        <div>
                            <div className="flex items-center gap-2">
                                <span className="font-bold text-white">PrimeSync</span>
                                <span className="rounded bg-blue-500/20 px-1.5 py-0.5 text-[8px] font-bold uppercase text-blue-400">AUTO</span>
                            </div>
                            <div className="flex items-center gap-1.5 text-[10px] text-gray-500">
                                {signal.side === "BUY" ? <ArrowUpRight size={10} className="text-green-400" /> : <ArrowDownRight size={10} className="text-red-400" />}
                                <span className={signal.side === "BUY" ? "text-green-400 font-bold" : "text-red-400 font-bold"}>{signal.side}</span>
                                <span>• {signal.time}</span>
                            </div>
                        </div>
                    </div>

                    <div>
                        <div className="text-right text-lg font-black tracking-tight text-white">{signal.pair}</div>
                        <div className={`flex items-center justify-end gap-1 text-[10px] font-bold uppercase tracking-wider ${signal.status === "EXECUTED" ? "text-green-400" : "text-yellow-400"}`}>
                            {signal.status === "EXECUTED" ? <CheckCircle size={10} /> : <Clock size={10} />}
                            {signal.status}
                        </div>
                    </div>
                </div>

                <div className="grid grid-cols-4 gap-2 border-t border-white/5 pt-4">
                    <div className="text-center">
                        <div className="text-[9px] font-bold uppercase tracking-wider text-gray-600">Entry</div>
                        <div className="font-mono text-xs font-medium text-white">{signal.entry}</div>
                    </div>
                    <div className="text-center">
                        <div className="text-[9px] font-bold uppercase tracking-wider text-gray-600">TP1</div>
                        <div className="font-mono text-xs font-medium text-green-400">{signal.tp1}</div>
                    </div>
                    <div className="text-center">
                        <div className="text-[9px] font-bold uppercase tracking-wider text-gray-600">TP2</div>
                        <div className="font-mono text-xs font-medium text-green-400">{signal.tp2}</div>
                    </div>
                    <div className="text-center">
                        <div className="text-[9px] font-bold uppercase tracking-wider text-gray-600">Lot</div>
                        <div className="font-mono text-xs font-medium text-blue-400">{signal.lotSize}</div>
                    </div>
                </div>

                <div className="mt-3 text-center text-[10px] text-gray-500">
                    ← Swipe to Execute or Reject →
                </div>
            </motion.div>
        </motion.div>
    );
};

export const SignalFeed = () => {
    const [activeSignals, setActiveSignals] = useState(signals);
    const [autoCopy, setAutoCopy] = useState(false);

    const handleExecute = (signal: any) => {
        console.log('Executing signal:', signal);
        setActiveSignals(prev => prev.filter(s => s.id !== signal.id));
    };

    const handleReject = (signal: any) => {
        console.log('Rejecting signal:', signal);
        setActiveSignals(prev => prev.filter(s => s.id !== signal.id));
    };

    return (
        <div className="space-y-4 pt-6">
            <div className="flex items-center justify-between">
                <div className="flex items-center gap-2">
                    <div className="h-2 w-2 rounded-full bg-blue-500 animate-pulse" />
                    <span className="text-xs font-bold uppercase tracking-widest text-gray-500">Live Signals</span>
                </div>

                <button
                    onClick={() => setAutoCopy(!autoCopy)}
                    className={`flex items-center gap-2 rounded-lg px-3 py-1.5 text-[10px] font-bold transition ${autoCopy ? 'bg-green-500/10 text-green-400 border border-green-500/20' : 'bg-white/5 text-gray-500 border border-white/5'}`}
                >
                    <Zap size={12} className={autoCopy ? 'fill-current' : ''} />
                    {autoCopy ? 'AUTO-COPY ON' : 'MANUAL'}
                </button>
            </div>

            <div className="space-y-3">
                {activeSignals.map((signal) => (
                    <SwipeableSignalCard
                        key={signal.id}
                        signal={signal}
                        onExecute={handleExecute}
                        onReject={handleReject}
                    />
                ))}

                {activeSignals.length === 0 && (
                    <motion.div
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        className="text-center py-12 text-gray-600"
                    >
                        <Zap size={32} className="mx-auto mb-2 opacity-20" />
                        <p className="text-sm">No pending signals</p>
                        <p className="text-xs">Waiting for PrimeSync...</p>
                    </motion.div>
                )}
            </div>
        </div>
    );
};
